* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  background-color: rgb(33, 33, 33);
  height: 50px;
  width: 100%;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-size: 15px;
    font-weight: 300;
    text-decoration: none;
    color: rgb(255, 255, 255); }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 44px; } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  height: 84px;

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_text {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.502);
    text-align: left;
    font-weight: 400;
    opacity: 0.6;
    margin-top: 6px; }

  .subheader_call {
    font-size: 12px;
    font-weight: 300;
    color: rgb(255, 255, 255);
    text-align: right;
    margin-top: 15px; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    font-size: 22px;
    font-weight: 400;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: right;
    line-height: 12px;
    text-decoration: none; }

  .subheader_btn {
    margin-top: 15px;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    width: 182px;
    height: 56px;
    font-size: 14px;
    font-weight: 400;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center;
    text-shadow: 0.707px 0.707px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  min-height: 800px;
  background: url('/img/BG_1.jpg') center (center / cover) no-repeat;
  padding: 150px 0 150px 0;

  .promo_header {
    font-size: 60px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
    line-height: 60px;
    margin: 0; }

  .promo_subheader {
    font-size: 36px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
    line-height: 36px;
    margin: 0;
    margin-top: 22px; }

  .promo_descr {
    display: block;
    margin-top: 23px;
    font-size: 18px;
    color: rgb(255, 255, 255);
    line-height: 1.333;
    text-align: center;
    font-weight: 400; }

  .promo_btn {
    display: block;
    margin: 0 auto;
    margin-top: 115px;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    width: 269px;
    height: 73px;
    font-size: 18px;
    font-weight: 400;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center;
    text-shadow: 0.707px 0.707px 0px rgba(0, 0, 0, 0.21); } }

.benefit {
  margin-top: 70px;

  .benefit_head {
    margin: 0  auto;
    padding: 8px 12px;
    width: 112px;
    background-color: rgb(0, 0, 0);
    font-size: 11px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center; } }

.benefit_h2 {
  margin-top: 38px;
  font-size: 38px;
  color: rgb(34, 34, 34);
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.benefit .benefit_block {
  margin-top: 41px;
  display: flex;
  justify-content: center;
  align-items: center;

  .benefit_round {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: rgb(30, 172, 199);
    width: 113px;
    height: 113px;
    margin-right: 25px; }

  .benefit_descr {
    width: 343px; }

  .benefit_subtitle {
    font-size: 18px;
    color: rgb(30, 172, 199);
    line-height: 1.235;
    font-weight: 400; }

  .benfit_text {
    font-size: 17px;
    color: rgb(30, 172, 199);
    line-height: 1.235;
    font-weight: 300; } }
